import React from 'react';
import { Layout, Breadcrumb, Spin } from 'antd';
import Header from './Header';
import Container from '@material-ui/core/Container';
import axios from 'axios';
import * as api from '../../api';
import Footer from './Footer';
import {Link} from 'react-router-dom';

const { Content } = Layout;

class PostDetail extends React.Component {
  state = {
    loaded: false,
    content: "",
    title: "",
    label: []
  }
  
  componentDidMount() {
    const myurl = api.api_post_detail.replace('221219972212199722121997', this.props.match.params.postid)
    
    axios.get(myurl).then(resp => {
      this.setState({
        loaded: true,
        content: resp.data.content,
        title: resp.data.title,
        label: resp.data.labels,
      })
    })

  }

  render() {
    return (
      <React.Fragment>
        <Container maxWidth="lg">
          <Header title="BAOLT" />
          <Layout className="layout">
            <Spin spinning={!this.state.loaded}>
              <Content style={{ padding: '0px 50px 50px 50px', minHeight: "65vh" }}>
                <Breadcrumb style={{ margin: '16px 0' }}>
                  <Breadcrumb.Item>
                    <Link to="/">Home</Link>
                  </Breadcrumb.Item>
                  {
                    this.state.label.map((val) => 
                      (<Breadcrumb.Item> {val} </Breadcrumb.Item>)
                    )
                  }
                  <Breadcrumb.Item>
                    {this.state.title}
                  </Breadcrumb.Item>
                </Breadcrumb>
                <div className="site-layout-content">
                  
                  <div dangerouslySetInnerHTML={{__html: this.state.content}} />
                  
                </div>
              </Content>
            </Spin>
          </Layout>
        </Container>
        <Footer title="" description="Make your life better than ever" />
      </React.Fragment>
    )
  }
}

export default PostDetail;