import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import GitHubIcon from '@material-ui/icons/GitHub';
import FacebookIcon from '@material-ui/icons/Facebook';
import YouTubeIcon from '@material-ui/icons/YouTube';
import Header from './Header';
import MainFeaturedPost from './MainFeaturedPost';
import FeaturedPost from './FeaturedPost';
import Main from './Main';
import Sidebar from './Sidebar';
import Footer from './Footer';
import axios from 'axios';
import * as api from '../../api';

const useStyles = theme => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
});

const mainFeaturedPost = {
  title: ' ',
  description:
    " ",
  image: 'https://1.bp.blogspot.com/-YALxj5bW8Yc/X7YgGzuV6wI/AAAAAAAAEbs/4nVoDb427fcCTGgcqVigqwwZTVc-0X0CQCLcBGAsYHQ/s16000/banner.jpg',
  imgText: 'description',
};

const posts = [
  {
    id: 1,
    title: "hello1",
  }
];

const sidebar = {
  title: 'About me',
  description:
    'BaoLT đẹp trai vô địch khắp vũ trụ.',
  archives: [
    { title: 'Label 1', url: '/' },
    { title: 'Label 2', url: '/' },
    { title: 'Label 3', url: '/' },
    { title: 'Label 4', url: '/' },
    { title: 'Label 5', url: '/' },
    { title: 'Label 6', url: '/' },
  ],
  social: [
    { name: 'GitHub', icon: GitHubIcon, url: "https://github.com/luthebao" },
    { name: 'Youtube', icon: YouTubeIcon, url: "https://www.youtube.com/c/BaoLuThe" },
    { name: 'Facebook', icon: FacebookIcon, url: "https://www.facebook.com/luthebao97/" },
  ],
};

class Blog extends React.Component {
  state = {
    featurePost: [],
    featurePost2: [],
  }

  componentDidMount() {
    const post_list = api.api_post_list + "&maxResults=500"
    axios.get(post_list)
    .then(resp => {
      resp.data.items.slice(0,2).map((value) => {
            const content = value.content;
            const parser = new DOMParser();
            const html = parser.parseFromString(content, 'text/html');
            const img = html.querySelector('img');
            const url = img ? img.src : '';
            var post = {
              title: value.title,
              date: Date(Date.parse(value.published)).slice(3,15),
              description: "",
              image: url,
              imageText: value.title,
              id: value.id
            }
            this.setState(prevState => ({
              featurePost: [...prevState.featurePost, post]
            }))
      });
      
      resp.data.items.slice(2,500).map((value) => {
        const content = value.content;
        const parser = new DOMParser();
        const html = parser.parseFromString(content, 'text/html');
        const img = html.querySelector('img');
        const url = img ? img.src : '';
        var post = {
          title: value.title,
          date: Date(Date.parse(value.published)).slice(3,15),
          image: url,
          id: value.id,
          avatar: value.author.image.url
        }
        this.setState(prevState => ({
          featurePost2: [...prevState.featurePost2, post]
        }))
  });
    
    })
  }

  render() {
    const {classes} = this.props;
    return (
      <React.Fragment>
        <CssBaseline />
        <Container maxWidth="lg">
          <Header title="BAOLT" ishow />
          <main>
            <MainFeaturedPost post={mainFeaturedPost} />
            <Grid container spacing={4}>
              {this.state.featurePost.map((post) => (
                <FeaturedPost key={post.title} post={post} />
              ))}
            </Grid>
            <Grid container spacing={5} className={classes.mainGrid}>
              <Main title="Latest Posts" posts={this.state.featurePost2} />
              <Sidebar
                title={sidebar.title}
                description={sidebar.description}
                archives={sidebar.archives}
                social={sidebar.social}
              />
            </Grid>
          </main>
        </Container>
        <Footer title="" description="Make your life better than ever" />
      </React.Fragment>
    )
  }
}

export default withStyles(useStyles)(Blog)
